.editor {
	display: flex;
	height: 100%;
}

.editor-item {
	flex-grow: 1;
	border: solid 1px;
	padding: 10px;
	margin: 2%;
}

@media (max-width: 600px) {
	.editor {
		flex-direction: column;
	}
	.editor-item {
		height: 46%;
		width: auto;
	}
}

.editor-tool-box {
	position: absolute;
	bottom: 50px;
	right: 50px;
	display: flex;
	flex-direction: row;
}
.editor-tool-wrapper {
	height: 50px;
	width: 50px;
	background-size: contain;
	background-color: rgb(255, 255, 255);
	color: #fff;
	border: solid #191946 2px;
	border-radius: 50px;
	margin-left: 10px;
}

@media (max-width: 600px) {
	.editor-tool-box {
		bottom: 20px;
		right: 20px;
	}
}
