.page {
	min-height: 100%;
	background-image: linear-gradient(to bottom right, #ffa60055, #aabc0055, #00ff0055);
	overflow-y: auto;
}

.page-main {
	width: 100vw;
	margin: 0 auto 0 auto;
}

@media (min-width: 640px) {
	.page-main {
		width: 80vw;
	}
}

@media (min-width: 960px) {
	.page-main {
		width: 60vw;
	}
}

.page-header {
	border-bottom: solid 1px black;
	text-align: center;
	padding: 25px 20px 15px 20px;
	font-weight: bold;
	font-size: 25px;
}

.page-body {
	padding: 30px 20px;
}

.page-title {
}

li {
	margin: 15px 0px;
}