html, body, #root {
	height: 100%;
}
.App {
  text-align: center;
  background-color: #f7f7f7;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.logo {
  height: 40vmin;
  pointer-events: none;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 18px;
	font-weight: bold;
	margin-top: 10px;
	text-decoration: underline;
	color: #000;
}
@media (min-width: 600px) {
	.header {
		font-size: 5vh;
	}
}
.header:hover {
	color: #00f;
	cursor:pointer;
}

.body {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 10px;
}

.footer {
	flex-grow: 0;
	font-size: 14px;
	color: #999;
	padding: 20px 40px;
}